<template>
  <main class="box-border h-full min-h-[calc(100vh-84px)] w-full pb-[84px]">
    <C-Back-Nav :title="'設計師業績分析報表'" :routeTarget="{ name: 'Report' }"></C-Back-Nav>

    <section class="relative z-0 mt-[52px] p-3">
      <div class="my-4 flex items-center justify-between">
        <div>
          <p><strong>設計師：</strong> {{ designerName +" | "+ GETTER_MECHANTNAME}}</p>
        </div>
      </div>
      <div class="flex flex-wrap items-center gap-2">
        <button
          class="rounded border px-2 py-1 focus:border-blue-300 focus:outline-none"
          @click="thisMonth"
        >
          本月
        </button>
        <button
          class="rounded border px-2 py-1 focus:border-blue-300 focus:outline-none"
          @click="lastMonth"
        >
          上個月
        </button>
        <button
          class="rounded border px-2 py-1 focus:border-blue-300 focus:outline-none"
          @click="setDay(2)"
        >
          前天
        </button>
        <button
          class="rounded border px-2 py-1 focus:border-blue-300 focus:outline-none"
          @click="setDay(1)"
        >
          昨天
        </button>
        <button
          class="rounded border px-2 py-1 focus:border-blue-300 focus:outline-none"
          @click="today"
        >
          今天
        </button>
        <vc-date-picker v-model="startDate" mode="date" :max-date="$dayjs().format('YYYY-MM-DD')">
          <template v-slot="{ inputValue, inputEvents }">
            <input
              class="rounded border px-2 py-1 focus:border-blue-300 focus:outline-none"
              placeholder="起始日"
              :value="inputValue"
              v-on="inputEvents"
            />
          </template>
        </vc-date-picker>
        <vc-date-picker v-model="endDate" mode="date" :max-date="$dayjs().format('YYYY-MM-DD')">
          <template v-slot="{ inputValue, inputEvents }">
            <input
              class="rounded border px-2 py-1 focus:border-blue-300 focus:outline-none"
              :value="inputValue"
              placeholder="結束日"
              v-on="inputEvents"
            />
          </template>
        </vc-date-picker>
        <button
          class="rounded border px-2 py-1 focus:border-blue-300 focus:outline-none"
          @click="setDisplayReference('TRUTH')"
        >
          實業績
        </button>
        <button
          class="rounded border px-2 py-1 focus:border-blue-300 focus:outline-none"
          @click="setDisplayReference('FAKE')"
        >
          虛業績
        </button>
      </div>

      <div class=" overflow-x-auto my-3 text-left text-sm">
        <div class="w-[768px] md:w-full break-words">
          <div
            class="mb-1 flex items-center justify-between border-b border-solid border-primary/50 bg-[#08424c]/80 p-1 text-white"
          >
            <div class="w-1/12">總業績</div>
            <div class="w-1/12">總客數</div>
            <div class="w-1/12">平均客單價</div>
            <div class="w-1/12">指定數</div>
            <div class="w-1/12">指定數%</div>
            <div class="w-1/12">不指定數</div>
            <div class="w-1/12">不指定%</div>
            <div class="w-1/12">店販業績</div>
            <div class="w-1/12">店販業績比%</div>
            <div class="w-1/12">會員卡業績</div>
            <div class="w-1/12">會員卡業績比%</div>
          </div>

          <div
            class="mb-1 flex items-center justify-between border-b border-solid border-primary/50 p-1"
          >
            <div class="w-1/12">{{ Math.round(totalPerformance) }}</div>
            <div class="w-1/12">{{ Math.round(totalOrderCount) }}</div>
            <div class="w-1/12">{{ Math.round(orderAveragePrice) }}</div>
            <div class="w-1/12">{{ Math.round(orderDesignateCount) }}</div>
            <div class="w-1/12">{{ Math.round(orderDesignatePercentage) }}%</div>
            <div class="w-1/12">{{ Math.round(orderNonDesignateCount) }}</div>
            <div class="w-1/12">{{ this.secondDecimalPoint(orderNonDesignatePercentage) }}%</div>
            <div class="w-1/12">{{ Math.round(productPerformance) }}</div>
            <div class="w-1/12">{{ this.secondDecimalPoint(productPerformancePercentage) }}%</div>
            <div class="w-1/12">{{ Math.round(membershipPerformance) }}</div>
            <div class="w-1/12">
              {{ this.secondDecimalPoint(membershipPerformancePercentage) }}%
            </div>
          </div>

          <!-- 第二部分 -->
          <div
            class="mb-1 flex items-center justify-between border-b border-solid border-primary/50 bg-[#08424c]/80 p-1 text-white"
          >
            <div class="w-1/12">項目</div>
            <div class="w-1/12">客數</div>
            <div class="w-1/12">客數比%</div>
            <div class="w-1/12">指定數</div>
            <div class="w-1/12">指定數%</div>
            <div class="w-1/12">不指定數</div>
            <div class="w-1/12">不指定%</div>
            <div class="w-1/12">單項業績</div>
            <div class="w-1/12">業績比%</div>
            <div class="w-1/12">平均單價</div>
          </div>
          <!-- 洗剪燙染 -->
          <div
            v-if="!isServiceTypeAnalyze"
            class="my-3 flex items-center justify-center border-solid border-primary/50 p-1"
          >
            <strong>尚無資料</strong>
          </div>

          <template v-else>
            <div
              class="mb-1 flex items-center justify-between border-b border-solid border-primary/50 p-1"
              v-for="serviceTypeAnalyze in serviceTypeAnalyzeList"
              :key="serviceTypeAnalyze.typeName"
            >
              <div class="w-1/12">{{ serviceTypeAnalyze.typeName }}</div>
              <div class="w-1/12">{{ serviceTypeAnalyze.orderTypeCount }}</div>
              <div class="w-1/12">
                {{
                  Math.round((serviceTypeAnalyze.orderTypePercentage + Number.EPSILON) * 100) / 100
                }}%
              </div>
              <div class="w-1/12">{{ serviceTypeAnalyze.orderTypeDesignateCount }}</div>
              <div class="w-1/12">
              {{Math.round((serviceTypeAnalyze.orderTypeDesignatePercentage + Number.EPSILON) * 100) / 100 }}%
              </div>
              <div class="w-1/12">{{ serviceTypeAnalyze.orderTypeNonDesignateCount }}</div>
              <div class="w-1/12">
              {{Math.round((serviceTypeAnalyze.orderTypeNonDesignatePercentage + Number.EPSILON) * 100) / 100 }}%
              </div>
              <div class="w-1/12">{{Math.round(serviceTypeAnalyze.currentOrderTypePerformance)}}
              </div>
              <div class="w-1/12">
              {{Math.round((serviceTypeAnalyze.currentOrderTypePerformancePercentage + Number.EPSILON) * 100) / 100}}%
              </div>
              <div class="w-1/12">{{Math.round(serviceTypeAnalyze.currentOrderTypeAveragePrice)}}
              </div>
            </div>
            <!-- 合計 -->
            <div
              class="mb-1 flex items-center justify-between border-b border-solid border-primary/50 p-1"
            >
              <div class="w-1/12"><strong>合計</strong></div>
              <!-- 客數 -->
              <div class="w-1/12">
                {{ getOrderTypeCountTotal }}
              </div>
              <!-- 客數比% -->
              <div class="w-1/12">100%</div>
              <!-- 指定數 -->
              <div class="w-1/12">
                {{ getOrderTypeDesignateCountCountTotal }}
              </div>
              <!-- 指定數% -->
              <div class="w-1/12">{{ getOrderTypeDesignatePercentageTotal }}%</div>
              <!-- 不指定數 -->
              <div class="w-1/12">
                {{ getOrderTypeNonDesignateCountTotal }}
              </div>
              <!-- 不指定% -->
              <div class="w-1/12">{{ getOrderTypeNonDesignatePercentageTotal }}%</div>
              <!-- 單項業績 -->
              <div class="w-1/12">
                {{ getCurrentOrderTypePerformanceTotal }}
              </div>
              <!-- 業績比 -->
              <div class="w-1/12">100%</div>
              <!-- 平均單價 -->
              <div class="w-1/12">
                {{ getServiceTypeAnalyzeListTotal }}
              </div>
            </div>
          </template>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import year from '@/assets/constant/year';
import month from '@/assets/constant/month';

export default {
  name: 'priceList-index',
  description: '報表的設計師業績分析報表',
  metaInfo: {
    title: 'BeautyOS 設計師',
    titleTemplate: '%s | 設計師業績分析表',
  },
  data() {
    return {
      startDate: this.$dayjs().valueOf(),
      endDate: this.$dayjs().valueOf(),
      year,
      month,
      displayReference: 'TRUTH',
      // 設計師名字:
      designerName: null,
      // 總業績:
      totalPerformance: null,
      // 總客數:
      totalOrderCount: null,
      // 平均客單價:
      orderAveragePrice: null,
      // 指定數:
      orderDesignateCount: null,
      // 指定數%:
      orderDesignatePercentage: null,
      // 不指定數:
      orderNonDesignateCount: null,
      // 不指定數%:
      orderNonDesignatePercentage: null,
      // 店販業績:
      productPerformance: null,
      // 店販業績%:
      productPerformancePercentage: null,
      // 會員卡業績:
      membershipPerformance: null,
      // 會員卡業績%:
      membershipPerformancePercentage: null,
      isServiceTypeAnalyze: true,
      serviceTypeAnalyzeList: [],
    };
  },
  mounted() {
    this.init();
  },
  methods: {
    async init() {
      await Promise.all([this.getDesignerPerforamnceAnalyzeReport()]).then(() => {});
    },
    setDisplayReference(data) {
      this.displayReference = data;
      this.getDesignerPerforamnceAnalyzeReport();
    },

    arrayEmpty(array) {
      return array.length === 0;
    },
    timeStampFormat(timeStamp) {
      const dt = new Date(timeStamp);
      const Year = dt.getFullYear().toString();
      const Month = (dt.getMonth() + 1).toString();
      const Day = dt.getDate().toString();
      return Year + Month + Day;
    },
    // 本月
    thisMonth() {
      const dt = new Date(Date.now());
      const y = dt.getFullYear();
      const m = dt.getMonth() + 1;
      const firstDay = new Date(y, m - 1, 1);
      this.startDate = +firstDay;
      this.endDate = +this.$dayjs().valueOf();
    },
    // // 上個月
    lastMonth() {
      const dt = new Date(Date.now());
      const y = dt.getFullYear();
      const m = dt.getMonth();
      const firstDay = new Date(y, m - 1, 1);
      const lastDay = new Date(y, m, 0);
      this.startDate = +firstDay;
      this.endDate = +lastDay;
    },
    // 今天
    today() {
      this.startDate = +this.$dayjs().valueOf();
      this.endDate = +this.$dayjs().valueOf();
    },
    // // 前天
    setDay(val) {
      const dt = new Date();
      const getDay = dt.setDate(dt.getDate() - val);
      this.startDate = +getDay;
      this.endDate = +getDay;
    },

    async getDesignerPerforamnceAnalyzeReport() {
      const input = {
        startDate: this.$dayjs(this.$dayjs(this.startDate).format('YYYY-MM-DD 00:00:00')).valueOf(),
        endDate: this.$dayjs(this.$dayjs(this.endDate).format('YYYY-MM-DD 00:00:00')).valueOf(),
      };
      const { displayReference } = this;
      const designerId = this.userInfo.user.id;
      const resp = await this.$api.designerPerforamnceAnalyzeReport(
        input,
        displayReference,
        designerId,
      );
      const { data, errors } = JSON.parse(JSON.stringify(resp.data));
      if (errors) return;
      if (data.designerPerforamnceAnalyzeReport[0]) this.setData(data.designerPerforamnceAnalyzeReport[0]);

      if (data.designerPerforamnceAnalyzeReport[0].serviceTypeAnalyze.length === 0) {
        this.isServiceTypeAnalyze = false;
      } else {
        this.isServiceTypeAnalyze = true;
        this.serviceTypeAnalyzeList = data.designerPerforamnceAnalyzeReport[0].serviceTypeAnalyze;
      }
    },
    setData(designerPerforamnceAnalyzeReport) {
      this.designerName = designerPerforamnceAnalyzeReport.designerName;
      this.totalPerformance = designerPerforamnceAnalyzeReport.totalPerformance;
      this.totalOrderCount = designerPerforamnceAnalyzeReport.totalOrderCount;
      this.orderAveragePrice = designerPerforamnceAnalyzeReport.orderAveragePrice;
      this.orderDesignateCount = designerPerforamnceAnalyzeReport.orderDesignateCount;
      this.orderDesignatePercentage = designerPerforamnceAnalyzeReport.orderDesignatePercentage;
      this.orderNonDesignateCount = designerPerforamnceAnalyzeReport.orderNonDesignateCount;
      this.orderNonDesignatePercentage = designerPerforamnceAnalyzeReport.orderNonDesignatePercentage;
      this.productPerformance = designerPerforamnceAnalyzeReport.productPerformance;
      this.productPerformancePercentage = designerPerforamnceAnalyzeReport.productPerformancePercentage;
      this.membershipPerformance = designerPerforamnceAnalyzeReport.membershipPerformance;
      this.membershipPerformancePercentage = designerPerforamnceAnalyzeReport.membershipPerformancePercentage;
    },
    // 取到小數點第二位
    secondDecimalPoint(val) {
      const result = Math.round((val + Number.EPSILON) * 100) / 100;
      return result;
    },
  },
  watch: {
    startDate() {
      if (!this.startDate) return;
      this.getDesignerPerforamnceAnalyzeReport();
    },
    endDate() {
      if (!this.endDate) return;
      this.getDesignerPerforamnceAnalyzeReport();
    },
  },
  computed: {
    ...mapState('userInfo', ['userInfo']),
    ...mapGetters('userInfo', ['GETTER_MECHANTNAME']),
    // 客數總計
    getOrderTypeCountTotal() {
      const result = this.serviceTypeAnalyzeList.reduce(
        (p, n) => ({ orderTypeCount: p.orderTypeCount + n.orderTypeCount }),
        { orderTypeCount: 0 },
      ).orderTypeCount;
      return result;
    },
    // 客數筆總計
    getOrderTypePercentageTotal() {
      const result = this.serviceTypeAnalyzeList.reduce(
        (p, n) => ({ orderTypePercentage: p.orderTypePercentage + n.orderTypePercentage }),
        { orderTypePercentage: 0 },
      ).orderTypePercentage;
      return this.secondDecimalPoint(result);
    },
    // 指定數總計
    getOrderTypeDesignateCountCountTotal() {
      const result = this.serviceTypeAnalyzeList.reduce(
        (p, n) => ({
          orderTypeDesignateCount: p.orderTypeDesignateCount + n.orderTypeDesignateCount,
        }),
        { orderTypeDesignateCount: 0 },
      ).orderTypeDesignateCount;
      return result;
    },
    // 指定數%總計
    getOrderTypeDesignatePercentageTotal() {
      const result = (this.getOrderTypeDesignateCountCountTotal / (this.getOrderTypeDesignateCountCountTotal + this.getOrderTypeNonDesignateCountTotal)) * 100;
      return this.secondDecimalPoint(result);
    },
    // 不指定數
    getOrderTypeNonDesignateCountTotal() {
      const result = this.serviceTypeAnalyzeList.reduce(
        (p, n) => ({
          orderTypeNonDesignateCount: p.orderTypeNonDesignateCount + n.orderTypeNonDesignateCount,
        }),
        { orderTypeNonDesignateCount: 0 },
      ).orderTypeNonDesignateCount;
      return result;
    },
    // 不指定數%總計
    getOrderTypeNonDesignatePercentageTotal() {
      const result = (this.getOrderTypeNonDesignateCountTotal / (this.getOrderTypeDesignateCountCountTotal + this.getOrderTypeNonDesignateCountTotal)) * 100;
      return this.secondDecimalPoint(result);
    },
    // 單項業績
    getCurrentOrderTypePerformanceTotal() {
      const result = this.serviceTypeAnalyzeList.reduce(
        (p, n) => ({
          currentOrderTypePerformance:
            p.currentOrderTypePerformance + n.currentOrderTypePerformance,
        }),
        { currentOrderTypePerformance: 0 },
      ).currentOrderTypePerformance;
      return Math.round(result);
    },
    // 業績比
    getCurrentOrderTypePerformancePercentageTotal() {
      const result = this.serviceTypeAnalyzeList.reduce(
        (p, n) => ({
          currentOrderTypePerformancePercentage:
            p.currentOrderTypePerformancePercentage + n.currentOrderTypePerformancePercentage,
        }),
        { currentOrderTypePerformancePercentage: 0 },
      ).currentOrderTypePerformancePercentage;
      return this.secondDecimalPoint(result);
    },
    // 平均單價
    getServiceTypeAnalyzeListTotal() {
      const result = this.serviceTypeAnalyzeList.reduce(
        (p, n) => ({
          currentOrderTypeAveragePrice:
            p.currentOrderTypeAveragePrice + n.currentOrderTypeAveragePrice,
        }),
        { currentOrderTypeAveragePrice: 0 },
      ).currentOrderTypeAveragePrice;
      return Math.round(result);
    },
  },
};
</script>

<style></style>
